@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .sidebar-svg {
    @apply w-6 h-6 text-zinc-500 transition duration-75 dark:text-zinc-400 group-hover:text-zinc-900 dark:group-hover:text-white group-hover:stroke-white
  }
  .sidebar-icon {
    @apply flex justify-center p-3 text-base font-normal text-zinc-900 rounded-lg dark:text-white hover:bg-zinc-100 dark:hover:bg-zinc-700
  }
}

body {
  margin: 0;
  font-family: Monaco, Menlo, Consolas, 'Droid Sans Mono', 'Inconsolata' 'Courier New', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: zincscale;
}

@font-face {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
